import Vue from "vue";
import five from "./fivexuUI/index";
Vue.use(five);
const vue = new Vue();

Vue.prototype.$warning = text => {
  vue.$fivexu.alert.show({ mesType: "warning", message: text });
};
Vue.prototype.$success = text => {
  vue.$fivexu.alert.show({ mesType: "success", message: text });
};
Vue.prototype.$dateFormat = (type, val) => {
  let date = val ? new Date(/^[0-9]*$/g.test(val) ? val * 1 : val) : new Date();
  let YYYY = date.getFullYear() + "";
  let m = date.getMonth() + 1;
  let MM = m > 9 ? m + "" : "0" + m;
  let d = date.getDate();
  let DD = d > 9 ? d + "" : "0" + d;
  let h = date.getHours();
  let hh = h > 9 ? h + "" : "0" + h;
  let $m = date.getMinutes();
  let mm = $m > 9 ? $m + "" : "0" + $m;
  let s = date.getSeconds();
  let ss = s > 9 ? s + "" : "0" + s;
  let obj = { YYYY, MM, DD, hh, mm, ss };

  return type.replace(/(YYYY)|(MM)|(DD)|(hh)|(mm)|(ss)/g, key => obj[key]);
};
Vue.prototype.$isBlur = cb => {
  var hiddenProperty =
    "hidden" in document
      ? "hidden"
      : "webkitHidden" in document
      ? "webkitHidden"
      : "mozHidden" in document
      ? "mozHidden"
      : null;
  var visibilityChangeEvent = hiddenProperty.replace(
    /hidden/i,
    "visibilitychange"
  );
  var onVisibilityChange = function() {
    if (!document[hiddenProperty]) {
      cb(true);
    } else {
      cb(false);
    }
  };
  document.addEventListener(visibilityChangeEvent, onVisibilityChange);
};
