<template>
  <div id="app">
    <video src="./assets/bg.mp4" autoplay loop>视频正在加载</video>
    <dotsRun></dotsRun>
    <transition name="opacity">
      <times class="time" v-if="isActive"></times>
    </transition>
    <ul class="nav">
      <router-link tag="li" :to="item.link" v-for="(item,index) in list" :key="index">{{item.title}}</router-link>
    </ul>
    <div class="router_view">
      <router-view />
    </div>
    <div class="footer">
      @版权申明: 页面UI来源FivexuUI
      <a
        href="https://fivexu.github.io/ui/#/button"
        target="black"
      >https://fivexu.github.io/ui/#/button</a>
    </div>
  </div>
</template>

<script>
const times = () => import('components/time/time.vue');
const dotsRun = () => import('components/dotsRun/dotsRun.vue');

const SHOW_TIME = 10000
export default {
  data() {
    return {
      // 是否在使用
      isActive: true,
      list: [
        { title: '推箱子', link: '/sokoban' },
        { title: '贪吃蛇', link: '/snake' },
        { title: '五子棋', link: '/fiveChess' },
        { title: '2048', link: '/2048' }
      ]
    }
  },
  methods: {
    mouseMove() {
      this.isActive = false;
      clearTimeout(this.timer);
      this.timer = setTimeout(() => {
        this.$fivexu.message.show({ message: `超过${SHOW_TIME / 1000}秒未动,将显示当前时间!` });
        this.isActive = true
      }, SHOW_TIME)
    }
  },
  destroyed() {
    clearTimeout(this.timer);
    window.removeEventListener('mousemove', this.mouseMove, true)
    window.removeEventListener('keydown', this.mouseMove, true)
  },
  mounted() {
    window.addEventListener('mousemove', this.mouseMove, true)
    window.addEventListener('keydown', this.mouseMove, true)
    this.$success(`超过${SHOW_TIME / 1000}秒未动,将有时间动画!`);
  },
  components: {
    times,
    dotsRun
  }
}
</script>

